import { FC } from 'react';
import { clsx } from 'clsx';

import { GridProps, GridItemProps } from './grid.interface';

const extractClasses = (classes: { [key: string]: string }, iterable: {}) =>
  Object.entries(iterable).map(([breakpoint, column]) => classes[`${breakpoint}-${column}`]);

export const Grid: FC<GridProps> = ({ columns, mode = 'auto', children }: GridProps) => {
  const defaultColumns = { xs: 1 };
  const mergedColumns = { ...defaultColumns, ...columns };
  const gridClasses = {
    'xs-1': 'zep-grid-cols-1',
    'xs-2': 'zep-grid-cols-2',
    'xs-3': 'zep-grid-cols-3',
    'xs-4': 'zep-grid-cols-4',
    'sm-1': 'zep-sm:zep-grid-cols-1',
    'sm-2': 'zep-sm:zep-grid-cols-2',
    'sm-3': 'zep-sm:zep-grid-cols-3',
    'sm-4': 'zep-sm:zep-grid-cols-4',
    'md-1': 'zep-md:zep-grid-cols-1',
    'md-2': 'zep-md:zep-grid-cols-2',
    'md-3': 'zep-md:zep-grid-cols-3',
    'md-4': 'zep-md:zep-grid-cols-4',
    'lg-1': 'zep-lg:zep-grid-cols-1',
    'lg-2': 'zep-lg:zep-grid-cols-2',
    'lg-3': 'zep-lg:zep-grid-cols-3',
    'lg-4': 'zep-lg:zep-grid-cols-4',
    'xl-1': 'zep-xl:zep-grid-cols-1',
    'xl-2': 'zep-xl:zep-grid-cols-2',
    'xl-3': 'zep-xl:zep-grid-cols-3',
    'xl-4': 'zep-xl:zep-grid-cols-4',
  };

  return (
    <div
      className={clsx(
        'zep-grid',
        'zep-gap-1.5',
        'zep-sm:zep-mx-1.5 zep-mx-1',
        'zep-content-stretch',
        mode === 'auto' ? extractClasses(gridClasses, mergedColumns) : 'zep-grid-cols-12',
      )}
      data-testid="zep-grid"
    >
      {children}
    </div>
  );
};

